import { Component, OnInit, ViewChild } from '@angular/core';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { Equipo, Mantenimiento } from '../../interfaces/mantenimiento';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ServiceMantenimientoService } from '../../service/service-mantenimiento.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import Swal from 'sweetalert2';
import { ActivatedRoute } from '@angular/router'; import { ServiceEquipoService } from '../../service/service-equipo.service';
;




@Component({
  selector: 'app-listar-mantenimiento',
  templateUrl: './listar-mantenimiento.component.html',
  styleUrls: ['./listar-mantenimiento.component.scss']
})
export class ListarMantenimientoComponent implements OnInit {
  breadCrumbItems: Array<{}>;
  mantenimientos: Mantenimiento[] = []
  mantenimiento: Mantenimiento
  mantenimientoSeleccionado!: Mantenimiento
  equipo!: Equipo
  idEquipo: number
  formulario!: FormGroup
  butttonText: string
  showLoading = false;
  /**DataTables */
  @ViewChild(DataTableDirective, { static: false }) dtElement: DataTableDirective
  dtOptions: any = {};
  dtTrigger: Subject<Mantenimiento> = new Subject<Mantenimiento>();

  constructor(private mantenimientoServicio: ServiceMantenimientoService, private equipoServicio: ServiceEquipoService, private modalService: NgbModal, private fb: FormBuilder, private route: ActivatedRoute) {
    this.formulario = this.initFomulario();
  }

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.idEquipo = params['id'];
    });

    this.breadCrumbItems = [{ label: 'Mantenimiento' }, { label: 'Listado', active: true }];
    this.initFomulario();
    this.dtOptions = {
      columnDefs: [
        { className: "", targets: [0, 1, 2, 3, 4] },
        { orderable: false, targets: [4] },
        { searchable: false, targets: [4] },
        { responsivePriority: 1, targets: -1 },
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: '//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json',
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: 'full_numbers',
      responsive: true,
    };

    this.obtenerEquipo();
    this.cargarDatos();


  }

  ngOnDestroy() {
    this.dtTrigger.unsubscribe()
  }
  private initFomulario(): FormGroup {
    return this.fb.group({
      fechainicio: ['', [Validators.required]],
      fechafin: ['', [Validators.required]],
      descripcion: ['', [Validators.required, Validators.minLength(8)]],
    })
  }


  reset() {
    this.formulario.reset()
  }

  openModal(content: any, reset: boolean) {
    if (reset) {
      this.mantenimientoSeleccionado = null;
      this.butttonText = "Guardar";
      this.formulario.reset()
      this.modalService.open(content,{backdrop: 'static', keyboard: false});
    }else{
      this.butttonText="Editar";
      this.modalService.open(content,{backdrop: 'static', keyboard: false});
    }
  }

  esCampoValido(campo: string) {
    const validarCampo = this.formulario.get(campo)
    return !validarCampo?.valid && validarCampo?.touched
      ? 'is-invalid' : validarCampo?.touched ? 'is-valid' : '';
  }

  cargarDatos() {
    this.mantenimientoServicio.mantenimientos(this.idEquipo).subscribe((resp: any) => {
      this.mantenimientos = resp;
      this.dtTrigger.next(null)
    });

  }

  eliminar(mantenimiento: Mantenimiento) {
    Swal.fire({
      title: '¿Está seguro de eliminar? ',
      text: `Esto no se puede revertir`,
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: "#a90000",
      cancelButtonColor: '#343a40',
      confirmButtonText: 'Sí, borrar',
      cancelButtonText: 'Cancelar'
    }).then((result) => {
      if (result.isConfirmed) {
        this.mantenimientoServicio.eliminar(mantenimiento).subscribe(resp => {
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Eliminado',
            text: `El registro ha sido eliminado`,
            icon: 'success',
          });
          this.reload()
        },
          (err: any) => {
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: 'Error',
              text: 'El registro no puede ser eliminado',
              icon: 'warning',
            });
          });
      } else if (result.isDenied) {
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: "Información",
          text: `Cambios no aplicados`,
          icon: "info",
        });
      }
    });
  }

  reload() {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      this.mantenimientos = []
      this.mantenimiento = null
      dtInstance.destroy()
      this.cargarDatos()
    })
  }

  editarlo(seleccion: Mantenimiento, modal: any) {
    this.mantenimientoSeleccionado = seleccion;
    this.openModal(modal, false);

  }

  accion() {
    if (this.mantenimientoSeleccionado != null) {
      this.editar();
    } else {
      this.guardar();
    }
  }

  guardar() {
    this.showLoading = true
    const fechainicio = this.formulario.get('fechainicio').value
    const fechafin = this.formulario.get('fechafin').value
    const descripcion = this.formulario.get('descripcion').value
    this.mantenimiento = new Mantenimiento(null, fechainicio, fechafin, descripcion, this.equipo)
    this.mantenimientoServicio.guardar(this.mantenimiento).subscribe((resp: any) => {
      if (resp) {
        this.showLoading = false
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: 'Éxito',
          text: `Almacenado correctamente`,
          icon: 'success',
        });
        this.modalService.dismissAll()
        this.formulario.reset()
        this.reload()
      } else {
        this.showLoading = false
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: 'Advertencia',
          text: `Ocurrió un problema`,
          icon: 'warning',
        });
      }

    })
    return Object.values(this.formulario.controls)
      .forEach(control =>
        control.markAsTouched())
  }

  editar() {
    this.showLoading = true
    this.mantenimientoSeleccionado.fechainicio = this.formulario.controls['fechainicio'].value
    this.mantenimientoSeleccionado.fechafin = this.formulario.controls['fechafin'].value
    this.mantenimientoSeleccionado.descripcion = this.formulario.controls['descripcion'].value

    this.mantenimientoServicio.editar(this.mantenimientoSeleccionado).subscribe(resp => {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Éxito',
        text: `Modificado correctamente`,
        icon: 'success',
      });
      this.modalService.dismissAll();
      this.mantenimientoSeleccionado = null;
      this.reset();
      this.reload();
    }, (err) => {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Error',
        text: `Algo fallo`,
        icon: 'error',
      });
    })
  }

  dateValidator() {
    const fechainicio = this.formulario.get('fechainicio').value;
    const fechafin = this.formulario.get('fechafin').value;
    if (fechainicio && fechafin && fechainicio > fechafin) {
      this.formulario.get('fechafin').setErrors({ dateMismatch: true });
    } else {
      this.formulario.get('fechafin').setErrors(null);
    }

  }
  async obtenerEquipo() {
    try {
      const data: Equipo = await this.equipoServicio.equipo(this.idEquipo).toPromise();
      this.equipo = data;
    } catch (error) {
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Advertencia',
        text: `Ocurrió un problema`,
        icon: 'warning',
      });
    }
  }


  openModalA(content) {
    this.modalService.open(content, { keyboard: false, scrollable: true, size: 'lg' });
  }


}
