import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ListarMantenimientoComponent } from './listar/listar-mantenimiento/listar-mantenimiento.component';

const routes: Routes = [
  { path: 'lista/:id', component: ListarMantenimientoComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MantenimientoRoutingModule { }
