import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Mantenimiento } from '../interfaces/mantenimiento';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ServiceMantenimientoService {
  url = `${environment.urlG}/mantenimiento`

  constructor(private http:HttpClient) { }

  public mantenimientos(id:number): Observable<any> {
    return this.http.get<any>(`${this.url}/${id}`)
  }

  guardar(data: Mantenimiento){
    return this.http.post(`${this.url}`,data)
  }
  editar(data: Mantenimiento){
    return this.http.put(`${this.url}/${data.id}`,data)
  }
  eliminar(data: Mantenimiento){
    return this.http.delete(`${this.url}/${data.id}`)
  }




}
